@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@600&display=swap');
body{
  /*background-color: #ffad33;*/
  background-image: url("https://newwallpapershd.com/wp-content/uploads/2018/11/Cartoon-Hd-Wallpaper-For-Android.jpg") ;
  background-size: cover;
}
.stylefrnds{
  text-align: center;
}
.stylefrnds h1{
  margin-top: 80px;
  font-family: 'Playfair Display', serif;
  font-size: 45px;
    color:yellow;
}
.stylefrnds button{
  margin-bottom: 0px;
  background-color: green;
  border: none;
  width:100px;
  height: 40px;
  color: white;
  font-family: 'Playfair Display', serif;
  font-size: 20px;
  border: 1px solid black;
  box-shadow: 3px 3px black;
  cursor: pointer;
}
.stylefrnds button:hover{
  background-color: green;
  box-shadow: none;
  border: 1px solid black;
}
.stylefrnds p{
  font-family: 'Playfair Display', serif;
  font-size: 25px;
  margin-bottom: 20px;
}
.stylefrnds h2{
  font-family: 'Playfair Display', serif;
  margin-bottom: 100px;
  font-size: 40px;
  color: red;
  margin-top:-20px;
}
.stylefrnds form textarea{
  margin-top: 5px;
  margin-bottom: 50px;
  font-family: 'Playfair Display', serif;
  font-size: 18px;
  font-weight: bold;
  resize: none;
  margin-left: -10px;
  width:400px;
  height: 70px;
  border:1px solid green;
}
.stylefrnds form p{
  margin-top: 40px;
  margin-left: -200px;
  font-size: 30px;
  text-align: center;
  margin-bottom: 5px;
  color:  black;
  font-weight: bold;
}
.stylefrnds form button{
  margin-bottom: 50px;
  position: absolute;
  margin-top: 15px;
}
