@import url('https://fonts.googleapis.com/css2?family=Grenze+Gotisch:wght@500&family=Permanent+Marker&display=swap');

.stylefrndsdata{
  text-align: center;
  margin-left: 500px;
  margin-right: 500px;
  padding: 50px;
  margin-bottom: 40px;
  background-image: linear-gradient(to bottom right, skyblue,blue);
  box-shadow: 10px 8px grey;
}
.stylefrndsdata:hover{
  cursor: pointer;
  box-shadow: none;
}
.stylefrndsdata img{
  width: 250px;
  height: auto;
}
.stylefrndsdata h3{
  font-family: 'Grenze Gotisch', cursive;
  color: white;
  font-weight: lighter;
  font-size: 35px;
}
.stylefrndsdata p{
  font-family: 'Grenze Gotisch', cursive;

  color:black;
  font-weight:bold;
  font-size: 25px;
}
